html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,code,em,img,small,strong,sub,sup,ol,ul,li,fieldset,form,label,legend,table,tbody,tfoot,thead,tr,th,td,article,aside,footer,header,nav,section,time,audio,video{margin:0;padding:0;border:0;font-size:100%;font-weight:inherit;vertical-align:baseline;background:transparent}article,aside,figure,footer,header,nav,section{display:block}html{-webkit-box-sizing:border-box;box-sizing:border-box;overflow-y:scroll}*,*:before,*:after{-webkit-box-sizing:inherit;box-sizing:inherit}img,object{max-width:100%}ul{list-style:none}table{border-collapse:collapse;border-spacing:0}th{font-weight:bold;vertical-align:bottom}td{font-weight:normal;vertical-align:top}input,select{vertical-align:middle}input[type="radio"]{vertical-align:text-bottom}input[type="checkbox"]{vertical-align:bottom}strong{font-weight:bold}label,input[type="file"],button{cursor:pointer}button,input,select,textarea{margin:0;border:0}button::-moz-focus-inner{padding:0;border:0}html{font-size:100%}body{font-size:62.5%}.wrapper{font-size:16px}body,input{font-family:'Open sans', sans-serif}h1,h2{font-weight:bold}h2{font-size:26px;font-size:calc(20px + 1vw)}body{padding:15px;padding:1vw;margin:0;color:#0e2c6c;background:#f2f3ff}.wrapper{max-width:1440px;min-width:320px;margin:0 auto}



