.header{margin-bottom:15px;color:white}.header .wrapper{display:-ms-flexbox;display:flex;background:#0e2c6c;border-radius:10px;overflow:hidden}.header__title{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;width:75%;padding-left:15px;background:-webkit-gradient(linear, left top, right top, from(black), to(#0e2c6c));background:-webkit-linear-gradient(left, black, #0e2c6c);background:-o-linear-gradient(left, black, #0e2c6c);background:linear-gradient(to right, black, #0e2c6c)}.header__title h1{padding:0;margin:0;font-size:26px;font-size:calc(20px + 1vw)}.header__logo{display:block;width:25%}.header__logo img{display:block;float:right}



